import { defineStore } from 'pinia'
import { ref, computed } from 'vue'
import { Howl } from 'howler'
import { musicApi } from '@/api/music'

export interface Song {
  id: number
  name: string
  artists: Array<{ id: number; name: string }>
  album?: { id: number; name: string }
  cover?: string
  duration?: number
  url?: string
}

export interface Playlist {
  id: number
  name: string
  cover?: string
  description?: string
  creator: User
  tracks: Song[]
  playCount?: number
}

export interface Artist {
  id: number
  name: string
  avatar?: string
}

export interface Album {
  id: number
  name: string
  cover?: string
  artist: Artist
}

export interface User {
  id: number
  nickname: string
  avatar?: string
}

export const useMusicStore = defineStore('music', () => {
  // 
  const currentSong = ref<Song | null>(null)
  const playlist = ref<Song[]>([])
  const currentIndex = ref(0)
  const likedSongs = ref<Set<number>>(new Set())
  
  // 
  const isPlaying = ref(false)
  const currentTime = ref(0)
  const duration = ref(0)
  const volume = ref(0.8)
  const isMuted = ref(false)
  
  // Howler
  let howl: Howl | null = null
  let updateTimer: number | null = null
  
  // 
  const hasNext = computed(() => currentIndex.value < playlist.value.length - 1)
  const hasPrev = computed(() => currentIndex.value > 0)
  const progress = computed(() => duration.value > 0 ? (currentTime.value / duration.value) * 100 : 0)
  
  // 
  const setCurrentSong = async (song: Song) => {
    currentSong.value = song
    await loadAndPlaySong(song)
  }
  
  const setPlaylist = (songs: Song[], index = 0) => {
    playlist.value = songs
    currentIndex.value = index
    if (songs[index]) {
      setCurrentSong(songs[index])
    }
  }
  
  // 
  const loadAndPlaySong = async (song: Song) => {
    try {
      // 
      if (howl) {
        howl.stop()
        howl.unload()
      }
      
      // URL
      const urlResponse = await musicApi.getSongUrl(song.id)
      const songUrl = urlResponse.data?.[0]?.url
      
      if (!songUrl) {
        console.error('')
        return
      }
      
      // Howl
      howl = new Howl({
        src: [songUrl],
        html5: true,
        volume: volume.value,
        onload: () => {
          duration.value = howl?.duration() || 0
        },
        onplay: () => {
          isPlaying.value = true
          startUpdateTimer()
        },
        onpause: () => {
          isPlaying.value = false
          stopUpdateTimer()
        },
        onend: () => {
          isPlaying.value = false
          nextSong()
        },
        onerror: (id, error) => {
          console.error(':', error)
          isPlaying.value = false
        }
      })
      
      // 
      howl.play()
      
    } catch (error) {
      console.error(':', error)
    }
  }
  
  // /
  const togglePlay = () => {
    if (!howl) return
    
    if (isPlaying.value) {
      howl.pause()
    } else {
      howl.play()
    }
  }
  
  // 
  const play = () => {
    if (howl && !isPlaying.value) {
      howl.play()
    }
  }
  
  // 
  const pause = () => {
    if (howl && isPlaying.value) {
      howl.pause()
    }
  }
  
  // 
  const nextSong = () => {
    if (hasNext.value) {
      currentIndex.value++
      setCurrentSong(playlist.value[currentIndex.value])
    }
  }
  
  // 
  const prevSong = () => {
    if (hasPrev.value) {
      currentIndex.value--
      setCurrentSong(playlist.value[currentIndex.value])
    }
  }
  
  // 
  const seekTo = (time: number) => {
    if (howl) {
      howl.seek(time)
      currentTime.value = time
    }
  }
  
  // 
  const setVolume = (vol: number) => {
    volume.value = vol
    if (howl) {
      howl.volume(vol)
    }
  }
  
  // 
  const toggleMute = () => {
    isMuted.value = !isMuted.value
    if (howl) {
      howl.mute(isMuted.value)
    }
  }
  
  // 
  const startUpdateTimer = () => {
    if (updateTimer) return
    
    updateTimer = setInterval(() => {
      if (howl && isPlaying.value) {
        currentTime.value = howl.seek() as number
      }
    }, 1000)
  }
  
  // 
  const stopUpdateTimer = () => {
    if (updateTimer) {
      clearInterval(updateTimer)
      updateTimer = null
    }
  }
  
  const toggleLike = (songId: number) => {
    // Ensure likedSongs is always a Set
    if (!(likedSongs.value instanceof Set)) {
      likedSongs.value = new Set(Array.isArray(likedSongs.value) ? likedSongs.value : [])
    }
    
    if (likedSongs.value.has(songId)) {
      likedSongs.value.delete(songId)
    } else {
      likedSongs.value.add(songId)
    }
  }
  
  const isLiked = (songId?: number) => {
    if (!songId) return false
    // Ensure likedSongs is always a Set
    if (!(likedSongs.value instanceof Set)) {
      likedSongs.value = new Set(Array.isArray(likedSongs.value) ? likedSongs.value : [])
    }
    return likedSongs.value.has(songId)
  }

  //  ()
  const setTestSong = () => {
    const testSong: Song = {
      id: 1982632526, // 
      name: '',
      artists: [{ id: 1, name: '' }],
      album: { id: 1, name: '' },
      cover: 'https://p1.music.126.net/9OctFTGTVGrQ2VfnUW5IpA==/109951168539669052.jpg',
      duration: 237000
    }
    setCurrentSong(testSong)
    setPlaylist([testSong], 0)
  }
  
  return {
    // 
    currentSong,
    playlist,
    currentIndex,
    likedSongs,
    isPlaying,
    currentTime,
    duration,
    volume,
    isMuted,
    
    // 
    hasNext,
    hasPrev,
    progress,
    
    // 
    setCurrentSong,
    setPlaylist,
    togglePlay,
    play,
    pause,
    nextSong,
    prevSong,
    seekTo,
    setVolume,
    toggleMute,
    toggleLike,
    isLiked,
    setTestSong
  }
}, {
  persist: {
    paths: ['likedSongs', 'currentSong', 'volume']
  }
})